<template>
	<v-app>
		<v-app-bar color="primary" dark app>
			<div class="d-flex align-center">
				<span :class="text - 2"> Future Jam 2022</span>
			</div>

			<v-spacer></v-spacer>

			<v-btn
				href="https://github.com/vuetifyjs/vuetify/releases/latest"
				target="_blank"
				elevation="2"
				text
			>
				<span class="mr-2">查看所有作品</span>
				<v-icon>mdi-open-in-new</v-icon>
			</v-btn>
		</v-app-bar>
		<v-stepper v-model="e1">
			<v-stepper-header>
				<v-stepper-step :complete="e1 > 1" step="1">
					阅读并同意参赛协议
				</v-stepper-step>

				<v-divider> </v-divider>

				<v-stepper-step :complete="e1 > 2" step="2" editable>
					提交作品
					<small>2022/05/16 12:00 GMT+8 截止</small>
				</v-stepper-step>

				<v-divider></v-divider>

				<v-stepper-step step="3">
					提交作品简介
					<small>2022/05/17 23:59 GMT+8 截止</small>
				</v-stepper-step>
			</v-stepper-header>

			<v-stepper-items>
				<v-stepper-content step="1">
					<v-card class="mb-12 card-wrapper" color="white">
						<rulesVue />
					</v-card>
					<v-checkbox
						v-model="agree_with_rules"
						label="我已阅读并同意参赛协议"
						color="primary"
					/>
					<v-btn color="primary" @click="e1 = 2" :disabled="!agree_with_rules">
						下一步
					</v-btn>
				</v-stepper-content>

				<v-stepper-content step="2">
					<v-card class="mb-12 card-wrapper" color="lighten-1">
						<h2>提交作品</h2>
						请点击下方按钮提交您的作品。<br />
						根据比赛规程，<span class="red--text"
							>每支小组需要提交一个压缩包，命名格式为：组名_游戏名.zip/7z/rar，例如“FromSoftware_Elden
							Ring.zip”，</span
						>，包含以下内容：
						<ul>
							<li class="red--text">必须：可执行游戏程序；</li>
							<li class="red--text">
								必须：说明是否参加腾讯游戏奖区外争夺，若有的话说明参加哪些奖区（如腾讯QQ机器人，麦吉太文休闲类游戏奖区，图形起源建模奖区）；
							</li>
							<li>
								可选：工程源文件，当评委无法运行提交的可执行程序时，将会尝试重新编译；
							</li>
							<li>可选：其他你认为必要的运行说明、操作说明等；</li>
							<li>
								可选：为了更好地展现作品创意，我们也欢迎你使用PPT、视频等方式来加强表达。
							</li>
						</ul>
						需要注意的是，<span class="red--text"
							>游戏介绍、游戏说明、演示等相关内容，可以在比赛截止后的一天内补充提交。本阶段只截止可执行文件与游戏工程。本阶段结束后，不接受除重大bug修复、多平台移植以外的可执行程序重新提交。</span
						>
						<br />
						<v-divider></v-divider>
						点击下方按钮前往清华云盘链接上传，<span class="red--text"
							>如果你的压缩包大于5G，请使用分卷压缩。</span
						>
						<br />
						<v-btn
							:loading="loading3"
							:disabled="loading3"
							color="blue-grey"
							class="ma-2 white--text"
							@click="openTsinghuaCloud"
						>
							Upload
							<v-icon right dark> mdi-cloud-upload </v-icon>
						</v-btn>
						<v-checkbox
							v-model="upload_success"
							label="我已按规定命名压缩包，并成功上传所有必须内容"
							color="primary"
						/>
					</v-card>

					<v-btn color="primary" @click="e1 = 3" :disabled="!upload_success">
						下一步
					</v-btn>

					<v-btn text @click="e1 = 1"> 上一步 </v-btn>
				</v-stepper-content>

				<v-stepper-content step="3">
					<v-card class="mb-12 card-wrapper">
						<v-form>
							<v-container>
								<v-row>
									<!-- <v-col cols="12" sm="12">
										<v-text-field
											v-model="team_name"
											counter="25"
											hint="填入组名"
											label="组名"
										></v-text-field>
									</v-col>
									<v-col cols="12" sm="12">
										<v-text-field
											v-model="title"
											counter="25"
											hint="填入你们小组的作品名称"
											label="游戏名称"
										></v-text-field>
									</v-col>
									<v-col cols="12" sm="12">
										<v-textarea
											name="input-7-1"
											label="作品描述"
											v-model="description"
											hint="你们创作了什么样的作品呢"
										></v-textarea>
									</v-col>

									<v-col cols="12" sm="12">
										<v-text-field
											v-model="additional_link"
											counter
											maxlength="256"
											hint="你可以将作品的演示视频、介绍文档、截图等各种无法在这里填下的内容，上传至Bilibili、云盘等平台，在这里填入相关链接"
											label="额外链接"
										></v-text-field>
									</v-col> -->
									<v-col cols="12" sm="12">
										你可以在下方的清华云盘链接中上传一些视频、图片、文档等任何你们项和我们分享的内容，提交要求与前项一致，请使用
										队名_作品名.zip/7z/rar 来上传压缩包。
										<br />
										<v-btn
											:loading="loading3"
											:disabled="loading3"
											color="blue-grey"
											class="ma-2 white--text"
											@click="openTsinghuaCloudDescription"
											>上传描述
											<v-icon right dark> mdi-cloud-upload </v-icon>
										</v-btn>
									</v-col>
								</v-row>
							</v-container>
						</v-form>
						<v-btn color="primary" @click="e1=2">上一页 </v-btn
						><br />
						<small>放心，截止时间之前都可以改</small>
					</v-card>
				</v-stepper-content>
			</v-stepper-items>
		</v-stepper>
		<v-snackbar v-model="snackbar">
			{{ snack_text }}
			<template v-slot:action="{ attrs }">
				<v-btn color="pink" text v-bind="attrs" @click="snackbar = false">
					Close
				</v-btn>
			</template>
		</v-snackbar>
	</v-app>
</template>


<script>
import rulesVue from "./rules.vue";
// import axios from "axios";
// import qs from 'qs';

export default {
	name: "SubmitPage",
	components: {
		rulesVue,
	},
	data() {
		return {
			e1: 1,
			agree_with_rules: false,
			upload_success: false,
			team_name: "",
			title: "",
			description: "",
			additional_link: "",
			snackbar: false,
			snack_text: "",
		};
	},
	watch: {
		e1() {
			let top = document.documentElement.scrollTop || document.body.scrollTop;
			const timeTop = setInterval(() => {
				document.body.scrollTop =
					document.documentElement.scrollTop =
					top -=
						10;
				if (top <= 0) {
					clearInterval(timeTop);
				}
			}, 4);
		},
	},
	methods: {
		openTsinghuaCloud() {
			window.open("https://cloud.tsinghua.edu.cn/u/d/1e75883902504b6b9db2/");
		},
		openTsinghuaCloudDescription() {
			window.open("https://cloud.tsinghua.edu.cn/u/d/1e75883902504b6b9db2/");
		},
		submitDescription() {
			if(!this.team_name || !this.title){
				this.snack_text = "请填写后再上传";
				this.snackbar =true;
				return
			}
			this.snackbar = false
			let submit_data = {
				team_name: this.team_name,
				name: this.title,
				description: this.description,
				additional_link: this.additional_link,
			};
			console.log(submit_data)
		},
	},
	mounted() {

	},
};
</script>

<style scoped>
.card-wrapper {
	width: 92%;
	left: 4%;
	padding: 20px;
	font-size: 14px;
	line-height: 1.5;
	color: #333;
}
</style>